@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@1,900&display=swap);
@import url(https://fonts.googleapis.com/css?family=Open+Sans:400,700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100&display=swap);
.users-list {
  list-style: none;
  margin: 0 auto;
  padding: 0;
  width: 90%;
  max-width: 50rem;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}
.paginator {
  padding-top: 2rem !important;
  padding-bottom: 4rem !important;
}
p {
  padding: 0rem;
  text-align: center;
}

.card.message-item__content {
  padding: 1rem;
  width: 100%;
  margin: 0.5rem;
}

.user-item__image {
  width: 4rem;
  height: 4rem;
  margin-right: 1rem;
}

.message-item__info h2 {
  font-size: 1.5rem;
  margin: 0 0 0.5rem 0;
  font-weight: normal;
  color: #ffd900;
}

.user-item:hover h2,
.user-item:active h2,
.user-item:hover h3,
.user-item:active h3 {
  color: #292929;
}

.user-item__info h3 {
  margin: 0;
}
.user-item__info h4 {
  font-size: 1rem;
  margin: 0 0 0.5rem 0;
  font-weight: normal;
  color: #bedef3;
}

.card {
  position: relative;
  margin: 0;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.26);
  border-radius: 20px;
  padding: 1rem;
  overflow: hidden;
  background-color: white;
  -webkit-border-radius: 20px;
  -moz-border-radius: 20px;
  -ms-border-radius: 20px;
  -o-border-radius: 20px;
  transition: -webkit-transform 0.5s !important;
  transition: transform 0.5s !important;
  transition: transform 0.5s, -webkit-transform 0.5s !important;
  -webkit-transition: transform 0.5s !important;
  -moz-transition: transform 0.5s !important;
  -ms-transition: transform 0.5s !important;
  -o-transition: transform 0.5s !important;
}
.card:hover {
  transform: scale(1.1) !important;
  -webkit-transform: scale(1.1) !important;
  -moz-transform: scale(1.1) !important;
  -ms-transform: scale(1.1) !important;
  -o-transform: scale(1.1) !important;
}

.modal {
  z-index: 100;
  position: fixed;
  top: 2rem !important;
  left: 10%;
  width: 80%;
  background: white;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.26);
  border-radius: 10px;
  -webkit-border-radius: 10px;
  -moz-border-radius: 10px;
  -ms-border-radius: 10px;
  -o-border-radius: 10px;
}

.modal__header {
  width: 100%;
  padding: 1rem 0.5rem;
  background: rgb(231, 44, 220);
  background: linear-gradient(
    90deg,
    rgba(231, 44, 220, 0.9192051820728291) 0%,
    rgba(31, 224, 200, 0.76234243697479) 100%
  );
  color: white;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
}

.modal__header h2 {
  margin: 0.5rem;
  color: white !important;
}

.modal__content {
  padding: 1rem 0.5rem;
}

.modal__footer {
  padding: 1rem 0.5rem;
  text-align: center;
}

.modal__content p {
  text-align: center;
  padding: 0.5rem;
}

@media (min-width: 768px) {
  .modal {
    left: calc(50% - 20rem);
    width: 40rem;
  }
}

.modal-enter {
  -webkit-transform: translateY(-10rem);
          transform: translateY(-10rem);
  opacity: 0;
}

.modal-enter-active {
  -webkit-transform: translateY(0);
          transform: translateY(0);
  opacity: 1;
  transition: all 200ms;
}

.modal-exit {
  -webkit-transform: translateY(0);
          transform: translateY(0);
  opacity: 1;
}

.modal-exit-active {
  -webkit-transform: translateY(-10rem);
          transform: translateY(-10rem);
  opacity: 0;
  transition: all 200ms;
}

.button {
  font: inherit;
  padding: 0.5rem 1.5rem;
  border: 1px solid #3c3b3c;
  border-radius: 20px;
  background: linear-gradient(to right, #6a0b62 0%, #105494 100%);
  /* background-clip: text; */
  font-family: "Roboto", sans-serif !important;
  font-style: italic;
  color: rgb(255, 255, 255);
  cursor: pointer;
  margin-right: 1rem;
  text-decoration: none;
  display: inline-block;
  -webkit-border-radius: 20px;
  -moz-border-radius: 20px;
  -ms-border-radius: 20px;
  -o-border-radius: 20px;
  margin: 1rem 2rem;
}

.button:focus {
  outline: none;
}

.button:hover,
.button:active {
  background: #ff4382;
  border-color: #ff4382;
}

.button--inverse {
  color: white;
  background: linear-gradient(to right, #6a0b62 0%, #105494 100%);
}

.button--inverse:hover,
.button--inverse:active {
  color: white;
  background: #ff0055;
}

.button--danger {
  background: linear-gradient(to right, #d12a18 0%, #760423 100%);
  border: 1px solid #3c3b3c;
}

.button--danger:hover,
.button--danger:active {
  background: #f34343;
  border-color: #f34343;
}

.button:disabled,
.button:hover:disabled,
.button:active:disabled {
  background: #ccc;
  color: #979797;
  border-color: #ccc;
  cursor: not-allowed;
}

.button--small {
  font-size: 0.8rem;
}

.button--big {
  font-size: 1.5rem;
}

